import { useState } from "react"
import { Lobby } from "./Lobby";
import { CommonModal } from "../shared/modals/CommonModal";
import { useProgress } from "../../contexts/ProgressContext";
import { Button, IconButton } from "../shared/Button";
import styled from "styled-components";
import { PersonIcon } from "../shared/svg/PersonIcon";
import { LetterModal } from "../shared/modals/LetterModal";
import { SCREENS } from "../../constants/screens";

const ButtonStyled = styled(Button)`
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: var(--spacing_x6);
    z-index: 10;
`;

const IconButtonStyled = styled(IconButton)`
    position: absolute;
    left: var(--spacing_x6);
    top: var(--spacing_x8);
    z-index: 1001;
    user-select: none;
`;

export const IntroRules = () => {
    const [part, setPart] = useState(0);
    const { user, next, setUserInfo } = useProgress();

    const getText = () => {
        if (part === 0) return (
            <p>
                     ,     .
                <br />
                <br />
                        .  ,   !
            </p>
        )
        if (part === 1) return (
            <p>
                       .
                <br />
                <br />
                       ,     , .
                <br />
                <br />
                       !
            </p>
        )

        if (part === 2 && user.isTarget) {
            return (
                <p>
                       ,  !
                    <br />
                    <br />
                     4      ,   .
                    <br />
                    <br />
                           .        .
                </p>
            )
        }
        if (part === 2) {
            return (
                <p>
                       ,      .
                    <br />
                    <br />
                            ,   ! </p>
            )
        }
    };

    const handleNext = () => {
        setPart(prev => prev + 1);
        setUserInfo({ seenStartInfo: true });
    };

    const handleClose = () => {
        next();
    };

    return (
        <>
            <Lobby isLaptop={part === 3} isLaptopLetter={part === 3} onLaptopClick={handleNext} plannerScreen={SCREENS.PLANNER1M}/>
            <CommonModal
                isOpen={part < 3}
                isDisabledAnimation={part !== 0}
                onClose={handleNext}
            >
                {getText()}
            </CommonModal>
            {part === 3 && (
                <ButtonStyled onClick={handleNext}>
                    
                </ButtonStyled>
            )}
            {part === 2 && (
                <IconButtonStyled>
                    <PersonIcon />
                </IconButtonStyled>
            )}
            <LetterModal isOpen={part > 3} isDarken checkedWeek={1} onClose={handleClose}/>
        </>
    );
};
